.App {
  text-align: center;
  background-color: black;
  height: 100%;
  min-height: 100vh;
}

.paragraph {
  color: white;
}

h1 {
  font-size: larger;
  color: white;
}

.homeImg {
  height: 75vmin;
}

.navBarItems {
  height: 80px;
  color: white;
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 1em;
  padding-left: 5px;
}

.navigation-active {
  display: grid;
}

.navigation-inactive {
  display: none;
}

a:visited {
  color: green;
}
a {
  font-weight: bold;
  text-decoration: none;
}

ul {
  list-style-type: none;
}
